body {
    padding: 0;
    background: #272727;
    color: white;
}

.App {
  position:absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  white-space: nowrap;
}

.App img {
  height: 200px;
  pointer-events: none;
}

.spin {
    animation: spin 5s linear infinite;
}

@keyframes spin {
    from {
        rotate: 0deg;
    }

    to {
      rotate: 360deg;
    }
}
